import React from 'react'
import styled from 'styled-components'

const Div = styled.div`
  display: flex;

  flex-direction: row wrap;

  .hour {
    background: rgba(10, 20, 40, 1);
  }
  .min {
    background: rgba(10, 20, 140, 1);
  }
`
const Input = styled.input`
  border: none;
  border-radius: 5px;
  align-items: center;
  display: flex;
  cursor: pointer;
  position: relative;
  width: 50px;
  height: 25px;
`

const hourOptions = [
  { value: '1:00', label: '1' },
  { value: '2:00', label: '2' },
  { value: '3:00', label: '3' },
  { value: '4:00', label: '4' },
  { value: '5:00', label: '5' },
  { value: '6:00', label: '6' },
  { value: '7:00', label: '7' },
  { value: '8:00', label: '8' },
  { value: '9:00', label: '9' },
  { value: '10:00', label: '10' },
  { value: '11:00', label: '11' },
  { value: '12:00', label: '12' },
]

const minOptions = [
  { value: '00', label: '00' },
  { value: '15', label: '15' },
  { value: '30', label: '30' },
  { value: '45', label: '45' },
]

const Timer = props => {
  return (
    <Div>
      {hourOptions.map(hour => (
        <Input
          className="hour"
          key={hour.value}
          readOnly
          name={hour.value}
          onClick={props.saveTime}
          value={hour.label}
        />
      ))}
      {minOptions.map(min => (
        <Input
          className="min"
          key={min.value}
          readOnly
          name="time"
          onClick={props.saveTime}
          value={min.label}
        />
      ))}
    </Div>
  )
}
export default Timer
